import { books } from "../data/books";
export const locStMainOptions = 'mainOptions';
export function getElementBySelector(element, type, selector) {
    const result = element.querySelector(selector);
    if (!result) {
        throw new Error(`Selector ${selector} didn't match any elements.`);
    }
    if (!(result instanceof type)) {
        throw new TypeError(`Selector ${selector} have wrong type`);
    }
    return result;
}
export function getLocalStorage(element, selector) {
    const result = element.getItem(selector);
    if (!result && selector === 'totalPrice') {
        return '0';
    }
    if (!result) {
        return '{}';
    }
    return result;
}
export function getMapBasketStorage(selector) {
    return new Map(Object.entries(JSON.parse(getLocalStorage(localStorage, selector))));
}
export let mainOptions = new Map();
export function getMainOptions() {
    mainOptions = new Map(Object.entries(JSON.parse(getLocalStorage(localStorage, locStMainOptions))));
}
export function setMainOptions() {
    const opt = JSON.stringify(Object.fromEntries(mainOptions));
    localStorage.setItem(locStMainOptions, opt);
}
export function checkBookId(id) {
    for (const book of books) {
        if (book.id === id) {
            return book;
        }
    }
    throw new Error('No such ID');
}
export function getHash(hash) {
    if (!hash)
        return "main-page" /* PageIds.MainPage */;
    const posOptions = hash.indexOf('?');
    if (posOptions < 0) {
        return hash.slice(1);
    }
    return hash.slice(1, posOptions);
}
export function createElementByTag(tagName, classNames, type, content) {
    const result = document.createElement(tagName);
    result.className = classNames;
    if (content) {
        result.textContent = content;
    }
    if (!(result instanceof type)) {
        throw new TypeError(`Selector ${tagName} have wrong type`);
    }
    return result;
}
export function getOptions(opt) {
    const result = new Map();
    const arr = opt.split('&');
    for (let i = 0; i < arr.length; i++) {
        const element = arr[i].split('=');
        if (element.length === 2) {
            result.set(element[0], element[1]);
        }
    }
    return result;
}
export function getBookID(options) {
    const res = options.get('id');
    if (typeof res === 'string') {
        const result = parseInt(res);
        if (books.some(val => val.id === result)) {
            return result;
        }
    }
    return -1;
}
export function getBasketOpt(options) {
    return options;
}
export const formatterUSD = new Intl.NumberFormat('en-US', {
    minimumFractionDigits: 2,
    style: 'currency',
    currency: 'USD',
});
export function getMainAddress() {
    setMainOptions();
    if (mainOptions.size === 0) {
        return `#${"main-page" /* PageIds.MainPage */}`;
    }
    const arr = [];
    mainOptions.forEach((val, key) => {
        arr.push(`${key}=${val}`);
    });
    return `#${"main-page" /* PageIds.MainPage */}?${arr.join('&')}`;
}
export function resetMainOptions() {
    mainOptions = new Map();
    setMainOptions();
}
