import { books } from "../../../data/books";
import { createElementByTag, getMainAddress, mainOptions, resetMainOptions, setMainOptions } from "../../../types/checks";
import noUiSlider from "nouislider";
import { delimeter, reg, SortOptions } from "../../../types/enums";
class Filters {
    constructor(sort, genre, publisher) {
        this.sort = sort;
        this.genre = genre;
        this.publisher = publisher;
    }
    renderFilters() {
        const section = document.createElement('section');
        section.className = 'filters';
        const baseDiv = createElementByTag('div', 'container filters__container', HTMLDivElement);
        const fWrapper = createElementByTag('div', 'filters__wrapper', HTMLDivElement);
        const filterGenre = createElementByTag('div', 'filters__genre', HTMLDivElement);
        const filterGenreTitle = createElementByTag('h3', 'filters__genre-title', HTMLHeadingElement, 'Genre');
        const filterPublisher = createElementByTag('div', 'filters__publisher', HTMLDivElement);
        const filterPublisherTitle = createElementByTag('h3', 'filters__publishers-title', HTMLHeadingElement, 'Publisher');
        const fSliders = createElementByTag('div', 'filters__sliders', HTMLDivElement);
        const fPrice = createElementByTag('div', 'filters__price', HTMLDivElement);
        const fPriceTitle = createElementByTag('h3', 'filters__price-title', HTMLHeadingElement, 'Price');
        const minPriceHTMLMax = createElementByTag('div', 'filters__price-minmax', HTMLDivElement);
        const minPriceHTML = createElementByTag('p', 'filters__price-min', HTMLParagraphElement, '0');
        const maxPriceHTML = createElementByTag('p', 'filters__price-max', HTMLParagraphElement, '0');
        const sliderPrice = createElementByTag('div', 'slider-price', HTMLDivElement);
        const fStock = createElementByTag('div', 'filters__stock', HTMLDivElement);
        const fStockTitle = createElementByTag('h3', 'filters__stock-title', HTMLHeadingElement, 'Stock');
        const minStockHTMLMax = createElementByTag('div', 'filters__stock-minmax', HTMLDivElement);
        const minStockHTML = createElementByTag('p', 'filters__stock-min', HTMLParagraphElement, '0');
        const maxStockHTML = createElementByTag('p', 'filters__stock-max', HTMLParagraphElement, '0');
        const sliderStock = createElementByTag('div', 'slider-stock', HTMLDivElement);
        const SWrapper = createElementByTag('div', 'filters__wrapper', HTMLDivElement);
        const fSort = createElementByTag('select', 'sort', HTMLSelectElement);
        const fReset = createElementByTag('button', 'filters__filter-reset', HTMLButtonElement, 'Reset Filters');
        const fCopy = createElementByTag('button', 'filters__filter-copy', HTMLButtonElement, 'Copy link');
        const fSearch = createElementByTag('div', 'filters__search', HTMLDivElement);
        const fSearchFrom = createElementByTag('form', 'filters__search-form', HTMLFormElement);
        const fsearchInput = createElementByTag('input', 'filters__search-input', HTMLInputElement);
        fsearchInput.type = 'search';
        fsearchInput.name = 'q';
        fsearchInput.placeholder = 'Search for books by keyword';
        const fsearchSubmit = createElementByTag('input', 'filters__search-submit', HTMLInputElement);
        fsearchSubmit.type = 'submit';
        fsearchSubmit.value = '';
        section.appendChild(baseDiv);
        baseDiv.appendChild(fWrapper);
        fWrapper.appendChild(filterGenre);
        filterGenre.appendChild(filterGenreTitle);
        fWrapper.appendChild(filterPublisher);
        filterPublisher.appendChild(filterPublisherTitle);
        fWrapper.appendChild(fSliders);
        fSliders.appendChild(fPrice);
        fPrice.appendChild(fPriceTitle);
        fPrice.appendChild(minPriceHTMLMax);
        minPriceHTMLMax.appendChild(minPriceHTML);
        minPriceHTMLMax.appendChild(maxPriceHTML);
        fPrice.appendChild(sliderPrice);
        fSliders.appendChild(fStock);
        fStock.appendChild(fStockTitle);
        fStock.appendChild(minStockHTMLMax);
        minStockHTMLMax.appendChild(minStockHTML);
        minStockHTMLMax.appendChild(maxStockHTML);
        fStock.appendChild(sliderStock);
        baseDiv.appendChild(SWrapper);
        SWrapper.appendChild(fSort);
        SWrapper.appendChild(fReset);
        SWrapper.appendChild(fCopy);
        baseDiv.appendChild(fSearch);
        fSearch.appendChild(fSearchFrom);
        fSearchFrom.appendChild(fsearchInput);
        fSearchFrom.appendChild(fsearchSubmit);
        for (let i = 0; i < SortOptions.length; i++) {
            const element = SortOptions[i];
            const opt = new Option(element.name, element.id);
            if (element.id === this.sort) {
                opt.selected = true;
            }
            fSort.appendChild(opt);
        }
        fCopy.onclick = function () {
            const url = window.location.href;
            navigator.clipboard.writeText(url).then(function () {
                console.log('URL copied!');
            }, function () {
                console.log('URL copy error');
            });
        };
        fReset.onclick = function () {
            resetMainOptions();
            window.location.hash = getMainAddress();
        };
        const genreCheckboxList = document.createElement('ul');
        genreCheckboxList.className = 'filters__genre-list';
        const publisherCheckboxList = document.createElement('ul');
        publisherCheckboxList.className = 'filters__publisher-list';
        const arrGenres = [];
        const arrAuthors = [];
        const prices = [];
        const stocks = [];
        for (const book of books) {
            prices.push(book.price);
            stocks.push(book.stock_balance);
            if (!arrGenres.includes(book.genre)) {
                arrGenres.push(book.genre);
                const genreCheckboxItem = document.createElement('li');
                const genreCheckboxInput = document.createElement('input');
                const genreCheckboxLabel = document.createElement('label');
                genreCheckboxItem.className = 'filters__genre-item';
                genreCheckboxItem.append(genreCheckboxInput, genreCheckboxLabel);
                genreCheckboxInput.type = 'checkbox';
                genreCheckboxInput.id = book.genre.replace(/ /g, '');
                genreCheckboxLabel.innerText = `${book.genre}`;
                genreCheckboxLabel.setAttribute('for', book.genre.replace(/ /g, ''));
                genreCheckboxList.append(genreCheckboxItem);
                if (this.genre.includes(genreCheckboxInput.id)) {
                    genreCheckboxInput.checked = true;
                }
                genreCheckboxItem.addEventListener('change', () => {
                    if (genreCheckboxInput.checked) {
                        this.genre.push(genreCheckboxInput.id);
                    }
                    else {
                        this.genre = this.genre.filter((val) => val !== genreCheckboxInput.id);
                    }
                    if (this.genre.length !== 0) {
                        mainOptions.set("genre" /* keyToMainOptions.Genre */, this.genre.join(delimeter));
                    }
                    else {
                        mainOptions.delete("genre" /* keyToMainOptions.Genre */);
                    }
                    window.location.hash = getMainAddress();
                });
            }
            if (!arrAuthors.includes(book.publisher)) {
                arrAuthors.push(book.publisher);
                const publisherCheckboxItem = document.createElement('li');
                const publisherCheckboxInput = document.createElement('input');
                const publisherCheckboxLabel = document.createElement('label');
                publisherCheckboxItem.className = 'filters__publisher-item';
                publisherCheckboxItem.append(publisherCheckboxInput, publisherCheckboxLabel);
                publisherCheckboxInput.type = 'checkbox';
                publisherCheckboxInput.id = book.publisher.replace(reg, '');
                publisherCheckboxLabel.innerText = `${book.publisher}`;
                publisherCheckboxLabel.setAttribute('for', book.publisher.replace(reg, ''));
                publisherCheckboxList.append(publisherCheckboxItem);
                console.log(this.publisher.includes(publisherCheckboxInput.id));
                if (this.publisher.includes(publisherCheckboxInput.id)) {
                    publisherCheckboxInput.checked = true;
                }
                publisherCheckboxInput.addEventListener('click', () => {
                    if (publisherCheckboxInput.checked) {
                        this.publisher.push(publisherCheckboxInput.id);
                    }
                    else {
                        this.publisher = this.publisher.filter((val) => val !== publisherCheckboxInput.id);
                    }
                    if (this.publisher.length !== 0) {
                        mainOptions.set("publisher" /* keyToMainOptions.Publisher */, this.publisher.join(delimeter));
                    }
                    else {
                        mainOptions.delete("publisher" /* keyToMainOptions.Publisher */);
                    }
                    window.location.hash = getMainAddress();
                });
            }
        }
        filterGenre.append(genreCheckboxList);
        filterPublisher.append(publisherCheckboxList);
        const minPrice = Math.min(...prices);
        const maxPrice = Math.max(...prices);
        minPriceHTML.innerText = String(minPrice);
        maxPriceHTML.innerText = String(maxPrice);
        noUiSlider.create(sliderPrice, {
            start: [0, 100],
            connect: true,
            range: {
                'min': minPrice,
                'max': maxPrice
            },
            margin: 1,
        });
        const minStock = Math.min(...stocks);
        const maxStock = Math.max(...stocks);
        minStockHTML.innerText = String(minStock);
        maxStockHTML.innerText = String(maxStock);
        noUiSlider.create(sliderStock, {
            start: [0, 100],
            connect: true,
            range: {
                'min': minStock,
                'max': maxStock
            },
            margin: 1,
        });
        fSort.addEventListener('change', () => {
            console.log('Chosen sort option: ', fSort.value);
            if (fSort.value === SortOptions[0].id) {
                mainOptions.delete("sort" /* keyToMainOptions.Sort */);
            }
            else {
                mainOptions.set("sort" /* keyToMainOptions.Sort */, fSort.value);
            }
            setMainOptions();
            window.location.hash = getMainAddress();
        });
        return section;
    }
}
export default Filters;
